.sidebar {
  iframe, .fb-page * {
    max-width: 100% !important;
  }
  .list-terms {
    display: block;
    li {
      font-size: 15px;
      flex: 1 1 100%;
      width: 100%;
      max-width: 100%;
    }
  }
  .widget {
    margin-bottom: 35px;
  }
  .widget ul {
    padding-left: 20px;
    margin:10px 0px;
    list-style: square;
  }
  .widget ul.list-terms {
    margin:0px -15px;
    padding:0px;
  }
  .widget .title {
    font-size: 25px;
    position: relative;
    margin-bottom: 30px;
    @include media(992px) {
      font-size: 19px;
    }
  }
  .widget .title:after {
    display: block;
    position: absolute;
    content:" ";
    bottom:-17px;
    left: 0px;
    width: 51px;
    height: 5px;
    background-color: #675A5E;
  }
  .widget .company-data {
    list-style: none;
    padding:0px;
  }
  .widget .company-data .fa {
    margin-right: 10px;
    font-size: 13px;
    color:$brand-primary;
  }
  .widget .search-wrap {
    position: relative;
  }
  .widget .search-wrap:after {
    content: "\f002";
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    text-decoration: inherit;
    position: absolute;
    font-size: 15px;
    line-height: 16px;
    color: #CCCCCC;
    top: 50%;
    right: 10px;
    margin: -8px 0 0 0px;
    z-index: 1;
  }
  .widget .search-wrap input {
    display: block;
    width: 100%;
  }

}

